export const fetchSentDocuments = async (
    platform: LimeWebComponentPlatform,
    externalId: string,
    session: ISession
): Promise<IDocument[]> => {
    const options: HttpOptions = {
        headers: {
            'ga-auth-token': session.access_token,
        },
        params: {
            external_id: externalId,
        },
    };
    const { documents } = await platform
        .get(PlatformServiceName.Http)
        .get('getaccept/sent-documents', options);

    return documents;
};

export const fetchTemplates = async (
    platform: LimeWebComponentPlatform,
    session: ISession,
    selectedTemplate: IListItem
): Promise<IListItem[]> => {
    const options: HttpOptions = {
        headers: {
            'ga-auth-token': session.access_token,
        },
    };
    const { templates = [] } = await platform
        .get(PlatformServiceName.Http)
        .get('getaccept/templates', options);

    return templates.map((template: ITemplate) => ({
        text: template.name,
        value: template.id,
        icon: 'document',
        iconColor: 'var(--lime-orange)',
        selected: selectedTemplate && selectedTemplate.value === template.id,
        thumbUrl: template.thumb_url,
    }));
};

export const fetchTemplateFields = async (
    platform: LimeWebComponentPlatform,
    session: ISession,
    limetype: string,
    record_id: number,
    selectedTemplate: IListItem
): Promise<ICustomField[]> => {
    const options: HttpOptions = {
        headers: {
            'ga-auth-token': session.access_token,
        },
        params: {
            template_id: selectedTemplate.value,
            limetype: limetype,
            record_id: record_id.toString(),
        },
    };
    const {
        data: { fields = [] },
    } = await platform
        .get(PlatformServiceName.Http)
        .get('getaccept/template-fields', options);

    return fields;
};

